export type OrderBookEntry = [number, number, number]; // [price, size, total]
export type RecentTradeEntry = {
  time: string;
  price: number;
  amount: number;
  side: 'buy' | 'sell';
};

// Seeded random number generator
function seededRandom(seed: number) {
  let state = seed;
  return () => {
    state = (state * 9301 + 49297) % 233280;
    return state / 233280;
  };
}

const generateOrderBook = (startPrice: number, levels: number, seed: number): { bids: OrderBookEntry[], asks: OrderBookEntry[] } => {
  const random = seededRandom(seed);
  const bids: OrderBookEntry[] = [];
  const asks: OrderBookEntry[] = [];
  let bidTotal = 0;
  let askTotal = 0;

  for (let i = 1; i <= levels; i++) {
    const bidPrice = startPrice - i * 0.5;
    const bidSize = random() * 5;
    bidTotal += bidSize;
    bids.push([bidPrice, bidSize, bidTotal]);

    const askPrice = startPrice + i * 0.5;
    const askSize = random() * 5;
    askTotal += askSize;
    asks.push([askPrice, askSize, askTotal]);
  }
  return { bids, asks };
};

const generateRecentTrades = (startPrice: number, count: number, seed: number): RecentTradeEntry[] => {
  const random = seededRandom(seed);
  const trades: RecentTradeEntry[] = [];
  let lastPrice = startPrice;
  const now = new Date(2024, 6, 22, 10, 0, 0); // Use a fixed time to avoid hydration issues

  for (let i = 0; i < count; i++) {
    const side = random() > 0.5 ? 'buy' : 'sell';
    const price = lastPrice + (random() - 0.5) * 2;
    const amount = random() * 2;
    const time = new Date(now.getTime() - i * 1000 * (random() * 5 + 1));
    trades.push({
      time: time.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit', second: '2-digit', hour12: false }),
      price: parseFloat(price.toFixed(2)),
      amount: parseFloat(amount.toFixed(3)),
      side,
    });
    lastPrice = price;
  }
  return trades;
};

// Create a seed from the symbol string
const getSeed = (symbol: string) => {
    let seed = 0;
    for (let i = 0; i < symbol.length; i++) {
        seed += symbol.charCodeAt(i);
    }
    return seed;
}

export const getMarketData = (symbol: string) => {
    const seed = getSeed(symbol);
    const random = seededRandom(seed);
    const basePrice = symbol.startsWith('BTC') ? 68000 : symbol.startsWith('ETH') ? 3600 : 150;
    const high = basePrice * 1.05;
    const low = basePrice * 0.95;
    const volume = random() * 10000;
    return {
        high: high.toFixed(2),
        low: low.toFixed(2),
        volume: volume.toFixed(0)
    }
}

export const getOrderBookData = (symbol: string) => {
    const seed = getSeed(symbol);
    const basePrice = symbol.startsWith('BTC') ? 68000 : symbol.startsWith('ETH') ? 3600 : 150;
    return generateOrderBook(basePrice, 15, seed);
}

export const getRecentTradesData = (symbol: string) => {
    const seed = getSeed(symbol);
    const basePrice = symbol.startsWith('BTC') ? 68000 : symbol.startsWith('ETH') ? 3600 : 150;
    return generateRecentTrades(basePrice, 20, seed);
}
