'use client';

import { Card, CardHeader, CardTitle, CardContent } from '@/components/ui/card';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table';
import { getOrderBookData, OrderBookEntry } from '@/lib/mock-data';
import { ScrollArea } from '@/components/ui/scroll-area';

interface OrderBookProps {
  symbol: string;
}

export function OrderBook({ symbol }: OrderBookProps) {
  const { bids, asks } = getOrderBookData(symbol);

  const renderRows = (data: OrderBookEntry[], type: 'bid' | 'ask') => {
    const sortedData = type === 'ask' ? data.slice().reverse() : data;
    const maxTotal = data.length > 0 ? data.reduce((max, current) => Math.max(max, current[2]), 0) : 0;

    return sortedData.map(([price, size, total], index) => {
      const depth = maxTotal > 0 ? (total / maxTotal) * 100 : 0;
      const bgClass = type === 'bid' ? 'bg-green-500/10' : 'bg-red-500/10';
      return (
        <TableRow key={index} className="text-xs">
            <TableCell className="p-1 font-mono text-left relative" style={{ color: type === 'bid' ? 'hsl(var(--chart-2))' : 'hsl(var(--chart-5))' }}>
                <div className={`absolute right-0 top-0 h-full ${bgClass}`} style={{ width: `${depth}%`, zIndex: 0 }}></div>
                <span className="relative z-10">{price.toFixed(2)}</span>
            </TableCell>
            <TableCell className="p-1 font-mono text-right z-10">{size.toFixed(4)}</TableCell>
            <TableCell className="p-1 font-mono text-right z-10">{total.toFixed(4)}</TableCell>
        </TableRow>
      );
    });
  };

  return (
    <Card>
      <CardHeader>
        <CardTitle>Order Book</CardTitle>
      </CardHeader>
      <CardContent className="p-0">
        <ScrollArea className="h-[300px]">
          <div className="grid grid-cols-1">
            <Table>
              <TableHeader>
                <TableRow className="text-xs">
                  <TableHead className="p-1 text-left">Price (USDT)</TableHead>
                  <TableHead className="p-1 text-right">Size ({symbol.replace('USDT', '')})</TableHead>
                  <TableHead className="p-1 text-right">Total</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {renderRows(asks, 'ask')}
              </TableBody>
            </Table>
            <div className="text-lg font-bold text-center py-2 border-t border-b">{getOrderBookData(symbol).bids[0][0].toFixed(2)}</div>
            <Table>
              <TableBody>
                {renderRows(bids, 'bid')}
              </TableBody>
            </Table>
          </div>
        </ScrollArea>
      </CardContent>
    </Card>
  );
}
